import React, { useState, useHistory } from "react";
import axios from "axios";
import "./SongDetails.css";

const SongDetails = ({ title, artist, length, id, isFavorite }) => {
  const [isDeleted, setIsDeleted] = useState(false);
  const [isFavorited, setIsFavorited] = useState({ isFavorite });
  let history = useHistory;

  const deleteSong = async () => {
    const response = await axios({
      url: `http://localhost:3000/songs/${id}`,
      method: "DELETE",
    });
    console.log(response.data);
    setIsDeleted(true);
  };

  if (isDeleted) {
    console.log("deleted!");
  }

  const toggleFavorite = () => {
    axios({
      url: `http://localhost:3000/songs/${id}`,
      method: "PUT",
      data: { isFavorite: isFavorited },
    })
      .then(() => setIsFavorited(!isFavorited))
      .catch(console.error);
    console.log(isFavorited);
  };

  return (
    <div>
      <div className="big-song-container">
      <div className="song-container-left">
        <p className="title">{title}</p>
        <p className="artist">{artist}</p>
        </div>
        <div className="song-container-right">
        <p className="length">{length}</p>
      <p className="heart" onClick={toggleFavorite}>
        &hearts;
      </p>
      {/* <button className="deleteButton" onClick={deleteSong}>
        Remove
      </button> */}
      </div>
      </div>
    </div>
  );
};

export default SongDetails;
